import React from 'react';
import { Grid, Paper } from '@material-ui/core';
import './box.css';
import { numberWithCommas } from '../../../utils/utils';


function BoxComponent(props) {

    return (
        <div className='grid-container'>
            <Grid container spacing={3}>
                <Grid item xs>
                    <Paper className='paper'
                    style={{borderBottom: '2px solid red'}}>
                        <span className='heading'> הרוגים </span>
                        <span className='number'> {numberWithCommas(props.selectedCountry.deaths || '-')} </span>
                        {(props.selectedCountry.todayDeaths) ? `${props.selectedCountry.todayDeaths} היום` : ''}
                    </Paper>
                </Grid>
                <Grid item xs>
                    <Paper className='paper'
                    style={{borderBottom: '2px solid orange'}}>
                        <span className='heading'> חולים </span>
                        <span className='number'>  {numberWithCommas(props.selectedCountry.cases || '-')} </span>
                        {(props.selectedCountry.todayCases) ? `${props.selectedCountry.todayCases} היום` : ''}
                    </Paper>
                </Grid>
                <Grid item xs>
                    <Paper className='paper'>
                        <span className='heading'> מחלימים </span>
                        <span className='number'> {numberWithCommas(props.selectedCountry.recovered || '-')} </span>
                    </Paper>
                </Grid>
        </Grid>
      </div>
    )

}

export default BoxComponent;