import React from 'react';
import { mystyle, title } from '../../utils/style';


const BoxInfo = function(props) {
    const classes = title();
    return (
      <div style={mystyle.box}>
        <img alt={props.item.country} src={props.item.countryInfo.flag} style={mystyle.img} />
        <span style={mystyle.title}>{props.item.country}</span>
        <div style={{padding: '1rem 1.3rem', color: '#EBEBEB'}}>
          <p className={classes.heading}>נדבקים</p> {props.item.cases} ({props.item.todayCases}+)
          <p className={classes.heading}>מקרי מוות</p> {props.item.deaths} ({props.item.todayDeaths}+)
          <p className={classes.heading}>החלימו</p> {props.item.recovered}
          <p className={classes.heading}>עודכן</p> {''+new Date(props.item.updated).toLocaleString("he-IL", {
            dateStyle: 'medium',
            timeStyle: 'short',
            month: '2-digis'
          })}
          <p className={classes.heading}>במצב קשה:</p> {props.item.critical}
        </div>
      </div>
    )
}

export default BoxInfo;