import React, { useState, useEffect } from 'react';
import Map from './components/world-map/Map';
import DataFetcher from './utils/utils';
import UserLocator from './components/world-map/UserLocator';
import SpecificApp from './components/country-specific/SpecificApp';

function App(props) {

  const [countries, updateCountries] = useState([]);
  const [userLoc, updateUserLoc] = useState([]);
  
  function childUpdate(childData) {
    updateUserLoc(childData);
  }

  useEffect(function () {
    if(countries !== []) {
      DataFetcher.getAllLocations().then(data => {
        updateCountries(data);
      })
    }
  }, []);
    if(props.panel !== true) {
      return (
        <div id="main">
          {(Object.keys(userLoc.data || {}).length > 0) ? 
          <Map api_key={DataFetcher.API_KEY} con={countries} center={userLoc.data}  /> :
          <UserLocator passDate={childUpdate} />}
        </div>
      )
    } else {
    return <SpecificApp countries={countries} />
    }
}

export default App;
