import React from 'react';
import './selectmenu.css';
import TextField from '@material-ui/core/TextField';
import { Grid } from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';

const countryToFlag = (flag, country) => {

    return (
        <img src={flag} alt={country} width={24} />
    )

}

function SelectMenu(props) {

    const handleChanges = (country) => {
        props.handleChange(country)
    }

    return (

            <Grid item xs={12}>
                <div className="container">
                    <Autocomplete
                        loading={true}
                        id="combo-box-demo"
                        options={props.countries}
                        getOptionLabel={(country) => country.country + `(${country.countryInfo.iso3})`}
                        style={{ width: '80%', margin: '0 auto'}}
                        onChange={(e, v) => handleChanges(v)}
                        renderOption={(option) => (
                            <React.Fragment>
                            <span>{countryToFlag(option.countryInfo.flag, option.country)}</span>
                            {option.country} ({option.countryInfo.iso3})
                            </React.Fragment>
                        )}
                        autoComplete={true}
                        disableClearable={true}
                        renderInput={(params) => <TextField {...params} label="State" placeholder="Select one"
                        style={{ direction: 'ltr', textAlign: 'left' }} variant="outlined"
                        />}
                    />
                </div>
            </Grid>

    )

}

export default SelectMenu;