import React, { useState } from 'react';
import SelectMenu from './SelectMenu/SelectMenu';
import BoxComponent from './3-boxes/BoxComponent';
import Header from './Header';
import ChartComponent from './chart/ChartComponent';

function SpecificApp(props) {

    const [selectedCountry, updateSelected] = useState([]);

    const handleCountry = (country) => {
        updateSelected(country)
    }

    return (
        <div>
            <Header selected={selectedCountry} />
            <SelectMenu countries={props.countries} selectedCountry={selectedCountry} handleChange={handleCountry} />
            <BoxComponent selectedCountry={selectedCountry} handleChange={handleCountry} />
            <ChartComponent selected={selectedCountry}/>
        </div>
    )

}

export default SpecificApp;