import React, { useEffect, useState } from 'react';
import Chart from 'chart.js';
import DataFetcher from '../../../utils/utils';
import { reciveChartData } from '../consts';

var myChart = null;

function DrawChart(props) {

    const ctx = React.createRef();

    useEffect(() => {
        
        if(myChart!=null){
            myChart.data.datasets = null;
            myChart.data.labels = null;
        }

        myChart = new Chart(ctx.current, reciveChartData(props));
    }, [props.deaths, props.cdata]);

    return (
        <div style={{margin: '0 auto', width: '80%', height: 'auto'}}>
            <canvas
                id="myChart"
                ref={ctx}
            />
        </div>
    )

}


function ChartComponent(props) {

    const [history, setHistory] = useState([]);
    const [oneLable, setLables] = useState([]);
    const [chartData, setChartData] = useState([]);
    const [chartDeaths, upadteDeaths] = useState([]);
    const [recoverd, setRecoverd] = useState([])

    useEffect(() => {

        async function fetch_data() {

            const con = (Object.keys(props.selected).length > 0) ? 
            props.selected.country : 'israel';
            const res = await DataFetcher.getHistorySpeceficCountrty(con, '7')
            .then(data => {
                upadteDeaths(Object.values(data.timeline.deaths));
                setLables(Object.keys(data.timeline.cases));
                setRecoverd(Object.values(data.timeline.recovered));
                setChartData(Object.values(data.timeline.cases));
                setHistory(data);
            })
            .catch(err => console.log(err))
            return;
        }

        fetch_data();
        
    }, [props.selected, upadteDeaths]);
    
    if(Object.keys(props.selected).length > 0) {
        return (
            <DrawChart country={oneLable} recoverd={recoverd} cdata={chartData} deaths={chartDeaths} />
        )
    } else {
        return ( <h1 style={{textAlign: 'center'}}>אנא בחר מדינה</h1> )
    }

    
}

export default ChartComponent;