

export const reciveChartData = (arr=new Array()) => {

    let data = {
        labels: [...arr.country],
        datasets: [{
            label: 'חולים',
            data: [...arr.cdata],
            backgroundColor: 'rgb(5, 159, 255)',
        },{
            label: 'החלימו',
            data: [...arr.recoverd],
            backgroundColor: 'rgb(62, 194, 45)'          
        }, {
            label: 'נפטרו',
            data: [...arr.deaths],
            backgroundColor: 'rgb(240, 80, 80)'
        }],
    }

    let chart_data = {
        type: "bar",
        data: data,
        backgroundColor: 'rgba(0, 0, 0, 1)',
        borderColor: "rgba(0, 123, 255, 0.9)",
        borderWidth: 2,
        options: {
            legend: {
                display: true,
                position: "bottom"
            },
            scales: {
                yAxes: [{
                    ticks: {
                        fontColor: "rgba(0,0,0,0.5)",
                        fontStyle: "bold",
                        beginAtZero: true,
                        maxTicksLimit: 10,
                        padding: 20
                    },
                    gridLines: {
                        drawTicks: false,
                        display: false
                    }
    }],
                xAxes: [{
                    gridLines: {
                        zeroLineColor: "rgba(0, 0, 0, 1)"
    },
                    ticks: {
                        padding: 20,
                        fontColor: "rgba(0,0,0,0.5)",
                        fontStyle: "bold"
                    }
                }]
            }
        }
    };

    return chart_data;

}