
const DataFetcher = {
    
    BASE_URL: 'https://corona.lmao.ninja/v2',
    API_KEY: 'AIzaSyBkwt4aE8tH57Tk8RIx3V6Qb9qbKWdOL1E',

    async getHistorySpeceficCountrty(state, days) {
        const res = await fetch(`${this.BASE_URL}/historical/${state}?lastdays=${days}`)
        .then(res => res.json());
        return res;
    },

    async getAllLocations() {
        const res = await fetch(`${this.BASE_URL}/countries?yesterday=false`)
        .then(res=>res.json()).catch(err => console.log(err));
        return res;
    },    

}

export const numberWithCommas = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default DataFetcher;