import React from 'react';
import { Button, Container } from '@material-ui/core';

function UserLocator(props) {

    const updateAppLocation = () => {
        window.navigator.geolocation.getCurrentPosition(function(position) {
            let [lat, long] = [position.coords.latitude, position.coords.longitude];
            const obj = {lat: lat, lng: long};
            return props.passDate({data: obj});
          });
    };

    return (
        <Container maxWidth='lg'>
            <Button onClick={updateAppLocation} variant="contained" color="primary">Get my location</Button>
        </Container>
    )

}

